@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Josefin Sans', sans-serif;
}

.container {
  width: 100%;
  height: 100vh;
  background-color: #6682AA;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.container__inner {
  width: 25%;
  height: 80vh;
  background-color: #F5F5F7;
  border-radius: 15px;
  box-shadow: 5px 5px 25px -5px #556684;
}

h1 {
  background: transparent;
  background-color: #876BA8;
  padding: 10px 0px 5px 0px;
  color: #fff;
}

input {
  text-align: center;
  color: #876BA8;
  height: 30px;
  top: 10px;
  border: none;
  font-size: 20px;
  border-bottom: 3px solid #876BA8;
  background: transparent;
  width: 60%;
  outline: none;
  font-weight: 500;
}

button {
  min-height: 40px;
  width: 40px;
  border-color: transparent;
  background-color: #876BA8;
  color: #fff;
  border-radius: 50%;
  font-size: 40px;
  border: none;
  outline: none;
  margin-left: 10px;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
  cursor: pointer;
}

button:hover {
  background-color: #20bf6b;
}

ol {
  margin-top: 30px;
  -webkit-padding-start: 0px;
          padding-inline-start: 0px;
}

ol li {
  padding-left: 0px;
  display: flex;
  text-align: left;
  font-size: 20px;
  font-weight: 500;
  color: #8f949c;
  min-height: 35px;
  align-items: center;
  text-transform: capitalize;
}

.todo__style {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: left;
}

.todo__style .faTimes {
  width: 20px;
  height: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #876BA8;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color: aliceblue;
  box-shadow: 5px 5px 15px -5px rgba(0, 0, 0, 0.3);
}

.todo__style .faTimes:hover {
  background-color: crimson;
}

@media (max-width: 760px) {
  .container {
    width: 50%;
  }
  .container__inner {
    width: 80%;
  }
  h1 {
    font-size: 20px;
    padding: 7px 0px 5px 0px;
    font-weight: 500;
  }
  input {
    height: 20px;
    font-size: 18px;
    width: 70%;
    font-weight: 300;
  }
  button {
    font-size: 20px;
  }
  ol {
    margin-top: 20px;
    -webkit-padding-start: 0px;
            padding-inline-start: 0px;
  }
  ol li {
    padding-left: 0px;
    font-size: 16px;
    font-weight: 400;
    min-height: 30px;
  }
}
